import { isUndefined } from '@rrc/utils'
import { RouteRecordRaw } from 'vue-router'

const routerRule = require.context('../', true, /^\.\/(pages|views)(\/[\w.-]+)?\/router.(js|ts)$/)

// 去中心化 加载路由
const moduleConfig = (r => {
  return r.keys().map(key => r(key).default)
})(routerRule)

export interface viewRouter {
  tabs: Array<RouteRecordRaw>
  global: Array<RouteRecordRaw>
}

/**
 *
 * 获取单个路由配置
 * @param {*} m
 * @param {*} attribute
 * @returns
 */
function getRouterConfig (m: viewRouter[], attribute: string):RouteRecordRaw[] {
  return m.map((module:any) => module[attribute]).filter((module: viewRouter) => !isUndefined(module)).reduce((routes:RouteRecordRaw[], route: RouteRecordRaw) => {
    return routes.concat(route)
  }, [])
}

export default {
  RouterOptions: {
    base: '/io-v3/'
  },
  mode: 'hash',
  // 全局路由 适用于 登录 注册 或者新开 窗口
  global: getRouterConfig(moduleConfig, 'global'),
  // 带tab 模块的页面
  tabs: getRouterConfig(moduleConfig, 'tabs'),
  // 重定向路由
  redirect: {
    path: '/:pathMatch(.*)*',
    redirect: '/'
  }
}
